import React, {useEffect, useState} from 'react';
import {useParams} from "react-router-dom";
import axios from "./apis/axios";
import {Box, Divider} from "@mui/material";
import FieldEditRender from "./FieldEditRender";

export default function MyEdit({edit}) {
    const {model, id} = useParams();
    const [fields, setFields] = useState({});
    const [formValues, setFormValues] = useState({});
    // const [data, setData] = useState({});
    useEffect(() => {
        const fetchData = async () => {
            const resultFields = await axios.options(`/${model}`);
            if (resultFields.status === 200) {
                setFields(resultFields.data.actions.POST);
                if (edit) {
                    const resultData = await axios.get(`/${model}/${id}`);
                    if (resultData.status === 200) {
                        setFormValues(resultData.data);
                    }
                }

            }

        }
        fetchData();
    },
        [model, id]
    );
    const fieldsKeys = Object.keys(fields);
    return (
        <React.Fragment>
            {
                fieldsKeys.map((fieldKey, index) => {
                    return (
                        <React.Fragment>
                            <Box
                                component="form"
                                sx={{
                                    '& .MuiTextField-root': {m: 1, width: '25ch'},

                                }}
                                noValidate
                                autoComplete="off"
                            >
                                <FieldEditRender
                                    key={index}
                                    field={fields[fieldKey]}
                                    id={fieldKey}
                                    // data={oldFormValues[fieldKey]}
                                    setFormValue={setFormValues}
                                    formValue={formValues}
                                />

                            </Box>
                            <Divider/>
                        </React.Fragment>
                    )
                })
            }
        </React.Fragment>
    )
}