import React, {useEffect, useState} from 'react';
import {Link, useParams} from "react-router-dom";
import axios from "./apis/axios";
import DataTable from "react-data-table-component";
import {Button} from "@mui/material";


export default function MyList(props) {
    const {model} = useParams();
    const [fields, setFields] = useState([])
    const [data, setData] = useState([])
    useEffect(() => {
        const fetchData = async () => {
            const resultFields = await axios.options(`/${model}`);
            if (resultFields.status === 200) {
                setFields(
                    [
                        {
                            name: resultFields.data.actions.POST["id"].label,
                            selector: row => `${ row.id }`,
                            sortable: true,
                            maxWidth: "300px"
                        },
                        {
                            name: resultFields.data.actions.POST["title"].label,
                            selector: row => `${ row.title }`,
                            sortable: true,
                            maxWidth: "500px"
                        },
                        {
                            name: 'Show',
                            maxWidth: "100px",
                            cell: (row) => {
                                return (<Button to={`show/${row.id}`} size="small" component={Link}
                                                variant="contained">Show</Button>)
                            }
                        },
                        {
                            name: 'Edit',
                            maxWidth: "100px",
                            cell: (row) => {
                                return (
                                    <Button to={`edit/${row.id}`} size="small" component={Link}
                                            variant="contained" color="warning">Edit</Button>
                                )

                            }
                        },
                        {
                            name: 'Delete',
                            maxWidth: "100px",
                            cell: (row) => {
                                return (
                                    <Button to={`delete/${row.id}`} size="small" component={Link}
                                            variant="contained" color="error">Delete</Button>
                                )
                            }
                        },
                    ]
                );
                // console.log("fields", fields);
                // resultFields.data.actions.POST
            }
            const resultData = await axios.get(`/${model}`);
            if (resultData.status === 200) {
                // console.log(resultData.data);
                setData(resultData.data)
            }
        }
        fetchData();
    }, [model]);

    return (
        <h1>
            <DataTable
                noDataComponent="اطلاعاتی برای نمایش وجود ندارد"
                noHeader={true}
                pagination={true}
                paginationPerPage="8"
                paginationRowsPerPageOptions={[8, 16, 24, 32, 40]}
                paginationComponentOptions={
                    {
                        // rowsPerPageText: 'تعداد سطر در جدول:',
                        // rangeSeparatorText: 'از',
                        noRowsPerPage: false,
                        selectAllRowsItem: false,
                        selectAllRowsItemText: 'All'
                    }
                }
                style={{fontSize: "30px"}}
                columns={fields}
                data={data}
            />
        </h1>
    )
}