import React from 'react';
import {Grid, Typography, CardMedia, Card} from "@mui/material";


export default function FieldShowRender({field, data}) {

    switch (field.type) {
        case "slug" :
        case "date" :
        case "string" :
        case "integer":
        case "choice" :
            return (
                <React.Fragment>
                    <Grid container spacing={2}>
                        <Grid item xs={2}>
                            {field.label}
                        </Grid>
                        <Grid item xs={10}>
                            <Typography variant="body1" gutterBottom>
                                {data}
                            </Typography>
                        </Grid>
                    </Grid>
                </React.Fragment>
            )
        case "image upload" :
            return (
                <React.Fragment>
                    <Grid container spacing={2}>
                        <Grid item xs={2}>
                            {field.label}
                        </Grid>
                        <Grid item xs={10}>
                            <Card sx={{maxWidth: 345,mb:2}}>
                                <CardMedia
                                    component="img"
                                    height="140"
                                    image={data}
                                    alt="green iguana"
                                />
                            </Card>
                        </Grid>
                    </Grid>
                </React.Fragment>
            )
        default:
            return (
                <React.Fragment>
                    <h1>Unknown-{field.type}-</h1>
                </React.Fragment>
            )

    }


}


