import React, {useEffect, useState} from 'react';
import {useParams} from "react-router-dom";
import axios from "./apis/axios";
import FieldShowRender from "./FieldShowRender";
import {Box, Divider} from "@mui/material";

export default function MyShow(props) {
    const {model, id} = useParams();
    const [fields, setFields] = useState({})
    const [data, setData] = useState({})
    useEffect(() => {
        const fetchData = async () => {
            const resultFields = await axios.options(`/${model}`);
            if (resultFields.status === 200) {
                setFields(resultFields.data.actions.POST);
                const resultData = await axios.get(`/${model}/${id}`);
                if (resultData.status === 200) {
                    setData(resultData.data);
                    // console.log(data)
                }
            }

        }
        fetchData();
    }, [model, id]);
    // console.log("fields", fields);
    // console.log("data", data);
    const fieldsKeys = Object.keys(fields);
    return (
        <React.Fragment>
            {

                fieldsKeys.map((fieldKey, index) => {
                    // console.log(fieldKey)
                    return (
                        <Box sx={{my: 3}}>
                            <FieldShowRender key={index} field={fields[fieldKey]} data={data[fieldKey]}/>
                            <Divider/>
                        </Box>
                    )

                })
            }
        </React.Fragment>
    )
}