import React from 'react'
import {
  BrowserRouter as Router,
  Switch,
  Route,
} from "react-router-dom";
import Panel from "./panel";

export default function App() {
  return (
    <Router>
      <div>

        <Switch>
          <Route path="/admin">
            <Panel />
          </Route>
        </Switch>
      </div>
    </Router>
  );
}
