import React, {useEffect, useState} from 'react';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import CssBaseline from '@mui/material/CssBaseline';
import Divider from '@mui/material/Divider';
import Drawer from '@mui/material/Drawer';
import IconButton from '@mui/material/IconButton';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemText from '@mui/material/ListItemText';
import MenuIcon from '@mui/icons-material/Menu';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import axios from "./apis/axios";
import {
    Link as RouterLink,
    Route,
    useRouteMatch,
    Switch,
    BrowserRouter as Router,
} from 'react-router-dom';
import {ListItemButton} from "@mui/material";
import MyList from "./MyList";
import MyDelete from "./MyDelete";
import MyEdit from "./MyEdit";
import MyShow from "./MyShow";

const drawerWidth = 240;

export default function Panel(props) {
    const {path, url} = useRouteMatch();
    const {window} = props;
    const [mobileOpen, setMobileOpen] = useState(false);
    const [menu, setMenu] = useState([]);
    const [menuStatus, setMenuStatus] = useState({});
    useEffect(() => {
        const fetchData = async () => {
            const result = await axios.get("");
            if (result.status === 200) {
                const modelsApiResponse = result.data;
                const modelsName = Object.keys(modelsApiResponse)
                setMenu(modelsName);
                setMenuStatus(
                     menu.reduce((a, v) => ({ ...a, [v]: false}), {})
                )
            }

        }
        fetchData();
    }, []);
    useEffect(() => {
    }, [url]);

    const handleDrawerToggle = () => {
        setMobileOpen(!mobileOpen);
    };

    function capitalizeFirstLetter(string) {
        return string.charAt(0).toUpperCase() + string.slice(1);
    }


    const drawer = (
        <div>
            {/*<Toolbar/>*/}
            <Divider/>
            <List>
                {menu.map((model, index) => {
                        // console.log(model, menuStatus, menuStatus[model])
                        return (
                            <ListItem
                                key={index}
                                disablePadding

                            >
                                <ListItemButton
                                    id={model}
                                    selected={menuStatus[model]}
                                    component={RouterLink}
                                    to={`${url}/${model}/list`}
                                    onClick={() => {
                                        let menuStatusTemp = menu.reduce((a, v) => ({ ...a, [v]: false}), {})
                                        setMenuStatus({...menuStatusTemp, [model]: true})
                                    }}
                                >
                                    <ListItemText primary={capitalizeFirstLetter(model)}/>
                                </ListItemButton>
                            </ListItem>
                        )
                    }
                )}
            </List>
        </div>
    );

    const container = window !== undefined ? () => window().document.body : undefined;


    return (
        <Router>
            <Box sx={{display: 'flex'}}>
                <CssBaseline/>
                <AppBar
                    position="fixed"
                    sx={{
                        width: {sm: `calc(100% - ${drawerWidth}px)`},
                        ml: {sm: `${drawerWidth}px`},
                    }}
                >
                    <Toolbar>
                        <IconButton
                            color="inherit"
                            aria-label="open drawer"
                            edge="start"
                            onClick={handleDrawerToggle}
                            sx={{mr: 2, display: {sm: 'none'}}}
                        >
                            <MenuIcon/>
                        </IconButton>
                        <Typography variant="h6" noWrap component="div">
                            Sabery Admin Panel
                        </Typography>
                    </Toolbar>
                </AppBar>
                <Box
                    component="nav"
                    sx={{width: {sm: drawerWidth}, flexShrink: {sm: 0}}}
                    aria-label="mailbox folders"
                >
                    {/* The implementation can be swapped with js to avoid SEO duplication of links. */}
                    <Drawer
                        container={container}
                        variant="temporary"
                        open={mobileOpen}
                        onClose={handleDrawerToggle}
                        ModalProps={{
                            keepMounted: true, // Better open performance on mobile.
                        }}
                        sx={{
                            display: {xs: 'block', sm: 'none'},
                            '& .MuiDrawer-paper': {boxSizing: 'border-box', width: drawerWidth},
                        }}
                    >
                        {drawer}
                    </Drawer>
                    <Drawer
                        variant="permanent"
                        sx={{
                            display: {xs: 'none', sm: 'block'},
                            '& .MuiDrawer-paper': {boxSizing: 'border-box', width: drawerWidth},
                        }}
                        open
                    >
                        {drawer}
                    </Drawer>
                </Box>
                <Box component="main" sx={{flexGrow: 1, p: 3}}>
                    <Toolbar/>
                    <div>
                        <Switch>
                            <Route exact path={`${path}/:model/list`}>
                                <MyList/>
                            </Route>
                            <Route exact path={`${path}/:model/add`}>
                                <MyEdit edit={false}/>
                            </Route>
                            <Route exact path={`${path}/:model/edit/:id`}>
                                <MyEdit edit={true}/>
                            </Route>
                            <Route exact path={`${path}/:model/show/:id`}>
                                <MyShow/>
                            </Route>
                            <Route exact path={`${path}/:model/delete/:id`}>
                                <MyDelete/>
                            </Route>
                        </Switch>
                    </div>
                </Box>
            </Box>
        </Router>
    );
}




