import React from 'react';
import {Grid, CardMedia, Card, TextField, Box} from "@mui/material";



export default function FieldEditRender({field, formValue, setFormValue, id}) {
    const handleChange = (event) => {
        // setFormValues({...formValues, id: event.target.value})
        // console.log({[event.target.id]: event.target.value})
        setFormValue(
            {...formValue, [id]: event.target.value}
            // oldFormValues => {
            //     console.log(oldFormValues,formValue)
            //     return {...formValue, [event.target.id]: event.target.value};
            // }
        )


    };
    switch (field.type) {
        case "slug" :
        case "date" :
        case "string" :
        case "integer":
        case "choice" :
            return (
                <React.Fragment>
                    <Grid container spacing={2}>
                        <Grid item xs={2} sx={{alignItems: 'center'}}>
                            <Box sx={{
                                display: 'flex',
                                alignItems: 'center',
                                p: 1,
                                m: 1,
                                bgcolor: 'background.paper',
                                height: 100,
                            }}
                            >
                                {field.label}
                            </Box>
                        </Grid>
                        <Grid item xs={10}>
                            <Box
                                sx={{
                                display: 'flex',
                                alignItems: 'center',
                                p: 1,
                                m: 1,
                            }}
                            >
                                <TextField
                                    required
                                    id="outlined"
                                    label="Required"
                                    value={formValue[id] || ''}
                                    onChange={handleChange}
                                />
                            </Box>
                        </Grid>
                    </Grid>
                </React.Fragment>
            )
        case "image upload" :
            return (
                <React.Fragment>
                    <Grid container spacing={2}>
                        <Grid item xs={2}>
                            <Box sx={{
                                display: 'flex',
                                alignItems: 'center',
                                p: 1,
                                m: 1,
                                bgcolor: 'background.paper',
                                height: 100,
                            }}
                            >
                                {field.label}
                            </Box>
                        </Grid>
                        <Grid item xs={10}>
                            <Card sx={{maxWidth: 345, my: 2}}>
                                <CardMedia
                                    component="img"
                                    height="140"
                                    image={formValue[id]}
                                    alt="green iguana"
                                />
                            </Card>
                        </Grid>
                    </Grid>
                </React.Fragment>
            )
        default:
            return (
                <React.Fragment>
                    <h1>Unknown-{field.type}-</h1>
                </React.Fragment>
            )

    }


}


